<template lang="pug">
  div.container
    img.header(:src="require('@/assets/test-out-header.png')")
    p.title {{ "Are you already familiar with this topic?" | localize }}
    p.text {{ "To skip this training, test your knowledge with a quiz. To learn more about the topic, start this training." | localize }}
    div.buttons
      button.button(@click="close") {{ "Start Training" | localize }}
      button.button.primary(@click="goToTestout") {{ "Take Quiz" | localize }}
</template>

<script>
import { Component } from 'vue-property-decorator';
import { getModule } from 'vuex-module-decorators';
import store from '@/store';
import { AppModule } from '@/store/modules/app';
import Scorm from '@bob-the-builder/kb4-scorm-client';
import { mixins } from 'vue-class-component';
import { ThemeConfModule } from '@/store/modules/theme';
import { BTBA } from '@/analytics/events/BTBA';

const ThemeStore = getModule(ThemeConfModule, store);
const AppState = getModule(AppModule, store);

@Component()
export default class Testout extends mixins(Scorm) {
  created() {
    this.init(ThemeStore.getScormOptions);
  }
  mounted() {
    this.$analytics.trackEvent(BTBA.showTestout);
  }
  goToTestout () {
    this.$analytics.trackEvent(BTBA.startTestout);
    let bookmark = this.getBookmark();
    bookmark.testout = { usedTestout: true, failedTestout: false };  
    this.setBookmark(bookmark);
    
    AppState.setTestout(true);
    this.$emit('close-disclaimer');
  }
  close () {
    this.$analytics.trackEvent(BTBA.rejectTestout);
    AppState.setTestout(false);
    this.$emit('close-disclaimer');
  }
}
</script>

